<template>
	<view class="rate-media">
		<view class="rate-media-body">
			<view class='key'>{{text}}</view>
			<view class="rate">
				<view
					class="rate-media-cell"
					v-for="(item,index) in max"
					:key="index"
					@click="clickStars(index)">
					<img 
						:style="{'width': size + 'rpx','height':size+'rpx','margin':margin+'rpx'}" 
						:src="value>=index+1?star_fill:star_empty"
					/>
				</view>
			</view>
			<view class="get_scrol" v-if="value>0" :style="{color:textColor}">{{value}}分</view>
		</view>
	</view>
</template>

<script>
	export default{
		name:'rate',
		data(){
			return{
				inValue:this.value
			}
		},
		props:{
			star_fill: {
				//亮星星
				type: [String],
				default: '/static/star.png'
			},
			star_empty: {
				//暗星星
				type: [String],
				default: '/static/star_empty.png'
			},
			score:{
				type:Array,
				default:function(){
					return ['1分','2分','3分','4分','5分']
				}
			},
			size: {// 星星的大小
				type: [Number, String],
				default: 48
			},
			value: {// 当前评分
				type: [Number, String],
				default: 0
			},
			text:{
				type:String
			},
			textColor:{
				type:String,
				default:"rgba(53,183,163,1);"
			},
			max: {// 最大评分
				type: [Number, String],
				default: 5
			},
			disabled: {// 是否可点击
				type: [Boolean, String],
				default: false
			},
			margin: {// 星星的间距
				type: [Number, String],
				default: '0 5'
			},
		},
		methods: {
			clickStars(i){
			  if (this.disabled) {
			  	return;
			  }
			  this.inValue = i+1
			  this.$emit("change", {
			  	value: this.inValue,
					score: this.score[i] ||'' 
			  });
			}
		}
	}
</script>

<style lang="stylus">
	.rate-media
		.rate-media-body
			display: flex;
			flex-direction row
			align-items center
			.key
				font-size:24upx;
				font-family:PingFangSC-Regular,PingFang SC;
				font-weight:400;
				color:rgba(31,35,52,1);
				margin-right:40upx
			.rate
				align-items center
				display flex
				margin-right:24upx
			.get_scrol
				font-size:24upx;
				font-family:PingFangSC-Medium,PingFang SC;
				font-weight:500;
				color:rgba(53,183,163,1);
				text-align right
		.rate-media-info
			display: flex;
			align-items: center;
			color: #999;
			font-size: 30rpx;
			.info_text
				margin:0 20rpx;
</style>
